.btn {
  min-width: 100px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px 15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.14;
  color: #f2f5f7;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: 0.3s ease;
}
.btn--primary {
  border: 1px solid #f2f5f7;
  max-width: 160px;
}
.btn--primary:hover {
  border-color: #00b2ff;
  color: #00b2ff;
}
.btn--secondary {
  max-width: 410px;
  padding: 15px 25px;
  font-size: 18px;
  line-height: 1.16;
  background-color: #00b2ff;
}
@media (max-width: 767px) {
  .btn--secondary {
    font-size: 14px;
  }
}
.btn--secondary:hover {
  color: #00b2ff;
  background-color: #ffffff;
}
.btn--tertiary {
  min-width: 180px;
  max-width: 220px;
  padding: 14px 12px 8px;
  color: #00b2ff;
  border: 1px solid #00b2ff;
}
.btn--tertiary:hover {
  color: #ffffff;
  background-color: #00b2ff;
}
.btn:focus-visible {
  outline: 2px solid #00b2ff;
  outline-offset: 2px;
}
.btn:active {
  opacity: 0.7;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

